
.navbar{
	width:100%;
	height:49px;
	position: fixed;
	left:0;
	bottom:0;
	z-index:10;
	display: flex;
	background: #f8f8f8;
}
.navbar:before{
	content:'';
	display: block;
	height:1px;
	width:100%;
	border-top:1px solid #CCCCCC;
	position:absolute;
	top:-1px;
	left:0;
	transform: scaleY(0.5);
}
.navbar a{
	flex:1;
	text-align: center;
	color: #666;
	position: relative;
}
.navbar a:last-child .weui-badge{
	display: none;
}
.navbar .iconfont{
	display: block;
	width: 100%;
	text-align: center;
	color: #666;
	margin-top:4px;
}
.navbar a.router-link-active .iconfont{
	color: #10AEFF;
}
.navbar a.router-link-active p{
	color: #10AEFF;
}
.page{
	width:100%;
	position: absolute;
	left:0;
	top:0;
	bottom:49px;
	height: auto;
}
.slideInRight{
	animation-duration: 300ms;
	transition-delay: 0.5s;
}
.slideOutRight{
	animation-duration: 300ms;
}
.weui-search-bar:before{
		border-top:none;
}
.list{	
	width:100%;
	z-index: 100;
}
.list li{
	border-bottom:1px solid #e5e5e5;
	padding:10px 15px;
	color:lightslategrey
}
li.no_task{
	text-align:center; 
	border:none;
	margin-top:30px;
}
li p{
	width:100%;
	height: 22px;
	line-height: 22px;
	margin-bottom:4px;
}
p .task_name{
	display:block;
	width:200px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size:16px;
	color: #666666;	
	margin-bottom: 3px;
	font-weight: 700;
	float:left;
}
p .do_user{
	font-size:14px;
	float:left;
}
p .status{
	line-height: 22px;
	padding: 0 2px;
	display: inline-block;
	text-align: center;
	background-color:#10AEFF;
	color:#fff;
	float:right;
}
.status.near{
	background-color:#FFBE00 ;
}
.status.over{
	background-color:#F76260;
}
.code{
	display: inline-block;
}
.date{
	float: right;
	text-align: center;
	color:#666;
}
.bottom{
	width:100%;
	height:30px;
	line-height: 30px;
	text-align: center;
}

































































































#finish .status{
	background-color:#09BB07 ;
}
#finish .wrapper{
	margin-bottom: 15px;
}


#td_detail{
	width:100%;
	position:absolute;
	left:0;
	top:0;
	bottom: 0;
	z-index:100;	
	background: #fff;
}
.vux-no-group-title,.vux-no-group-title{
	margin-top:0 !important;
}
#td_detail .taskDetail{
	width:100%;
	overflow: hidden;
	position:absolute;
	left:0;
	top:0;
	bottom:0;
}
#td_detail ul{
	border-bottom: 1px solid #ccc;
	padding: 15px;
}
#td_detail ul li{
	line-height: 30px;
	display: flex;
}
ul li span{
	color:lightslategrey;
}
ul li i{
	color:#000;
	flex:1;
	margin-left:5px;
}
.btn{
	position: absolute;
	left: 0;
	bottom:0;
	width:100%;
	text-align: center;
	display: flex;
	height:40px;
	line-height: 40px;
	color:#fff;
	font-size:16px;
}
.btn span{
	flex:1;
}
.btn span:first-child{
	background-color: #09bb07;
}
.btn span:last-child{
	background-color: #E64340;
}
.process{
	width:100%;
}
.process.limit{
	height: 370px;
	border-bottom: 1px solid #ccc;
}
.process .agree_status{	
	margin:15px;
}
.agree_status .info{
	border:1px solid #ccc;
	padding: 15px 10px;
	position: relative;
	vertical-align: middle;
}
.allInfo:first-child{
	margin-top:10px;
}
.allInfo:last-child{
	margin-bottom:50px;
}
.info .iconfont{
	position: absolute;
	top:20px;
}
.allInfo:last-child .arrow{
	display: none;
}
.info p{
	display: inline-block;
	margin-left:30px;
}
.info p span{
	display: block;
	margin-bottom:5px;
}
.taskStatus{
	position: absolute;
	right:26px;
	top:13px;
}
.process_over{
	border: 1px solid #ccc;
    padding: 10px;
	text-align: center;
	color:#E64340;
}
.weui-icon{
	position:absolute;
	right:25px;
	top:35px;
}
.arrow{
	text-align: center;
}

